//   retry_admin_auth
handlePasswordInput.action('retry_admin_auth', async (ctx) => {
    try {
        await ctx.answerCbQuery(); //    callback
        await ctx.scene.reenter(); //  
    } catch (error) {
        console.error('Error in retry_admin_auth:', error);
        //       
        await ctx.scene.reenter();
    }
});

