import { Scenes, Markup } from 'telegraf';
import { MyContext } from '../../../types/MyContext';
import laravelService from '../../../../services/laravelService';
import * as fs from 'node:fs';
import * as path from 'path';
import axios from 'axios';
import logger from '../../../../utils/logger/loggerTelegram';

export const changePhotoScene = new Scenes.BaseScene<MyContext>('change_photo_scene');

const MIN_SIZE = 800; //      
const MAX_SIZE = 2000; //      
const MAX_FILE_SIZE = 5 * 1024 * 1024; // 5   

//   
changePhotoScene.enter(async (ctx: MyContext) => {
    const message = `
 *   *

 *  :*
   (1:1)
   ${MIN_SIZE}x${MIN_SIZE}  ${MAX_SIZE}x${MAX_SIZE} 
  JPG/JPEG
    5 
     
     
   

 *:*
  
   
   
    

       :`;

    await ctx.replyWithMarkdown(message, 
        Markup.inlineKeyboard([
            [Markup.button.callback('   ', 'cancel_photo')],
            [Markup.button.callback('   ', 'photo_help')],
            [Markup.button.callback('    ', 'mainmenu')]
        ])
    );
});

//   
changePhotoScene.on('photo', async (ctx) => {
    try {
        const photo = ctx.message.photo[ctx.message.photo.length - 1];
        const file = await ctx.telegram.getFile(photo.file_id);
        
        if (!file.file_path) {
            throw new Error('    ');
        }

        //   
        if (photo.width < MIN_SIZE || photo.height < MIN_SIZE) {
            await ctx.reply(
                `   .   ${MIN_SIZE}x${MIN_SIZE} .`,
                Markup.inlineKeyboard([
                    [Markup.button.callback('   ', 'retry_photo')],
                    [Markup.button.callback('   ', 'size_help')]
                ])
            );
            return;
        }

        //   
        if (Math.abs(photo.width - photo.height) > 10) { //   10 
            await ctx.reply(
                '     (  1:1).',
                Markup.inlineKeyboard([
                    [Markup.button.callback('   ', 'retry_photo')],
                    [Markup.button.callback('   ?', 'crop_help')]
                ])
            );
            return;
        }

        await ctx.reply('  ...');

        //  
        const fileUrl = `https://api.telegram.org/file/bot${process.env.TELEGRAM_BOT_TOKEN_MASTER}/${file.file_path}`;
        
        const response = await axios({
            url: fileUrl,
            method: 'GET',
            responseType: 'arraybuffer'
        });

        //   
        if (response.data.length > MAX_FILE_SIZE) {
            await ctx.reply(
                '    5 . ,     .',
                Markup.inlineKeyboard([
                    [Markup.button.callback('   ', 'retry_photo')],
                    [Markup.button.callback('   ?', 'size_reduce_help')]
                ])
            );
            return;
        }

        //   
        const tempDir = path.join(__dirname, '../../../../../temp');
        if (!fs.existsSync(tempDir)) {
            fs.mkdirSync(tempDir, { recursive: true });
        }

        //   
        const tempFilePath = path.join(tempDir, `${ctx.from.id}_${Date.now()}.jpg`);
        fs.writeFileSync(tempFilePath, response.data);

        try {
            const updateResult = await laravelService.updateMasterPhoto(
                ctx.from.id,
                tempFilePath
            );

            if (updateResult?.success) {
                await ctx.reply(
                    '   !\n\n     .',
                    Markup.inlineKeyboard([
                        [Markup.button.callback('   ', 'view_profile')],
                        [Markup.button.callback('    ', 'mainmenu')]
                    ])
                );
            } else {
                throw new Error('   ');
            }
        } finally {
            //   
            if (fs.existsSync(tempFilePath)) {
                fs.unlinkSync(tempFilePath);
            }
        }

    } catch (error) {
        logger.error('Error updating photo:', error);
        await ctx.reply(
            '     .\n\n,      .',
            Markup.inlineKeyboard([
                [Markup.button.callback('  ', 'retry_photo')],
                [Markup.button.callback('    ', 'mainmenu')]
            ])
        );
    }
});

//  
changePhotoScene.action('retry_photo', async (ctx) => {
    await ctx.answerCbQuery('   ');
    return ctx.scene.reenter();
});

changePhotoScene.action('cancel_photo', async (ctx) => {
    await ctx.answerCbQuery('   ');
    return ctx.scene.enter('main');
});

